@import url('https://fonts.googleapis.com/css?family=Roboto|Merriweather');

body {
  font-size: 13px;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
  background: #e1e1e1;
}

.header {
  position: absolute;
  top: 0px;
  left: 0px;
  width: calc(100% - 30px);
  padding: 15px;
  background: #37393d; /* Old browsers */
  background: -moz-linear-gradient(45deg,  #37393d 0%, #27282b 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg,  #37393d 0%,#27282b 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg,  #37393d 0%,#27282b 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#37393d', endColorstr='#27282b',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
} 

.subheader {
  display: block;
  margin: 0px auto;
  width: 960px;
}

@media (max-width: 960px) {
  .subheader {
    width: 100% !important;
  }

  .logo {
    position: absolute !important;
    left: 20%;
    top: 60px;
    width: 88px !important;
    height: 88px !important;
  }

  nav {
    width: 100% !important;
    margin: 0px !important;
  }

  nav li {
    display: block !important;
  }

  nav li a {
    display: block !important;
    width: 80% !important;
    text-align: right !important;
  }
}

nav {
  display: inline-block;
  margin: 0px 0px 0px 80px;
}

.header ul {
  margin: 0px;
  padding: 0px;
}

.header ul li {
  display: inline-block;
}

.header ul li a {
  display: block;
  width: 70px;
  min-width: 70px;
  padding: 15px 7.5px;
  color: white;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}

nav a:hover {
  color: #ffea00 !important;
}

nav .active {
  color: #ffea00 !important;
}

.logo {
  position: absolute;
  display: block;
  width: 48px;
  height: 48px;
  background-image: url('./images/logo.png');
  background-size: contain;
  z-index: 1;
}

@media (max-width: 960px) {
  .container {
    width: 100% !important;
    top: 204px !important;
    padding: 0px !important;
  }

  .subcontainer {
    margin: 0px auto !important;
    margin-top: 15px !important;
    margin-left: 5px !important;
    width: calc(100% - 10px) !important;
  }

  .standing {
    display: none !important;
  }

  .nextgame {
    display: block !important;
    width: calc(100% - 50px) !important;
    float: none !important;
  }

  .lastgame {
    display: none !important;
  }

  .news {
    width: 100% !important;
  }

  .news .news-1 {
    width: 100% !important;
  }

  .single-article {
    width: 100% !important;
  }

  .single-article h1 {
    width: 100% !important;
    padding: 15px 0px !important;  
  }

  .team > div {
    width: calc(50% - 4px) !important;
    margin-right: 8px !important;
    margin-bottom: 8px !important;
  }

  .team > div:nth-child(2n) {
    margin-right: 0px !important;
  }
}

.container {
  position: absolute;
  top: 74px;
  left: 0px;
  width: calc(100% - 30px);
  padding: 15px;
  background-color: #e1e1e1;
}

.subcontainer {
  margin: 0px auto;
  width: 960px;
}

.nextgame {
  float: right;
  width: 610px;
  color: white;
  background: #37393d; /* Old browsers */
  background: -moz-linear-gradient(45deg,  #37393d 0%, #27282b 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg,  #37393d 0%,#27282b 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg,  #37393d 0%,#27282b 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#37393d', endColorstr='#27282b',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  padding: 25px;
}

.nextgame img {
  display: block;
  margin: 0px auto;
  margin-bottom: 10px;
  width: 64px;
  height: 64px;
}

.nextgame ul {
  margin: 0px;
  padding: 0px;
}

.nextgame ul li {
  display: inline-block;
  vertical-align: top;
  text-align: center;
}

.nextgame ul li:nth-child(1), .nextgame ul li:nth-child(3) {
  width: 25%;
  text-transform: uppercase;
  text-overflow: ellipsis;
  overflow: hidden !important;
  max-width: 25%;
  white-space: nowrap !important;
}

.nextgame ul li:nth-child(2) {
  width: 50%;
  text-transform: uppercase;
}

.nextgame ul li:nth-child(2) a {
  display: block;
}

.nextgame ul li:nth-child(2) a.nextgame-time {
  color: white;
  font-size: 31px;
}

.nextgame ul li:nth-child(2) a.nextgame-date {
  color: #b9f6ca;
  margin-bottom: 22px;
}

.nextgame ul li:nth-child(2) a.nextgame-league {
  color: #ffea00;
}

.standing {
  display: inline-block;
  width: 100px;
  padding: 28px 25px 32px 25px;
  color: #000;
  text-align: center;
  background: #8fc400; /* Old browsers */
  background: -moz-linear-gradient(45deg,  #8fc400 0%, #8fc400 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg,  #8fc400 0%,#8fc400 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg,  #8fc400 0%,#8fc400 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#8fc400', endColorstr='#8fc400',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}

.standing h3 {
  color: #000;
  font-size: 41px;
  text-align: center;
  text-shadow: 0px 0px 5px rgba(255,255,255,1);
  margin: 0px;
}

.lastgame {
  float: right;
  width: 100px;
  padding: 15.2px 25px 15.2px 25px;
  color: #000;
  text-align: center;
  background: #8fc400; /* Old browsers */
  background: -moz-linear-gradient(45deg,  #8fc400 0%, #8fc400 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg,  #8fc400 0%,#8fc400 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg,  #8fc400 0%,#8fc400 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#8fc400', endColorstr='#8fc400',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}

.lastgame h3 {
  color: #000;
  font-size: 41px;
  text-align: center;
  text-shadow: 0px 0px 5px rgba(255,255,255,1);
  margin: 0px;
}

.news {
  display: block;
  width: 960px;
  height: 320px;
  margin: 15px 0px 0px 0px;
}

.news div {
  display: inline-block;
  background: rgb(80, 80, 80);
}

.news .news-1 {
  float: left;
  width: 699px;
  height: 320px;
  border-right: 1px solid #fff;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}

.news .news-1:hover > .newsTitle a, .news .news-2:hover > .newsTitle a, .news .news-3:hover > .newsTitle a {
  color: #ffea00 !important;
}

.news .news-2 {
  width: 260px;
  height: 160px;
  border-top-right-radius: 2px;
}

.news .news-3 {
  width: 260px;
  height: 160px;
  border-bottom-right-radius: 2px;
}

.news .news-1 .newsTitle {
  display: block;
  position: relative;
  top: calc(300px - 20px);
  padding: 10px;
  color: #fff;
  font-size: 17px;
  background: rgba(0,0,0,0.6);
  border-bottom-left-radius: 2px;
  text-overflow: ellipsis;
  overflow: hidden !important;
  max-width: 100%;
  white-space: nowrap !important;
}

.news .news-1 .newsTitle:hover {
  cursor: pointer;
}

.news .news-1 .newsTitle a {
  color: white;
  text-decoration: none;
}

.news .news-2 .newsTitle, .news .news-3 .newsTitle {
  display: block;
  position: relative;
  top: calc(156px - 20px);
  padding: 5px;
  color: #fff;
  font-size: 12px;
  background: rgba(0,0,0,0.6);
  text-overflow: ellipsis;
  overflow: hidden !important;
  max-width: 100%;
  white-space: nowrap !important;
}

.news .news-2 .newsTitle:hover, .news .news-3 .newsTitle:hover {
  cursor: pointer;
}

.news .news-2 .newsTitle a, .news .news-3 .newsTitle a {
  color: white;
  text-decoration: none;
}

.news .news-3 .newsTitle {
  border-bottom-right-radius: 2px;
}

.teamTitle {
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.375rem;
  margin: 15px 0px;
  background-color: #D3D6D7;
  border-left: .1875rem solid #9C1737;
  border-radius: 2px;
  padding: .5rem .9375rem;
}

.team {
  margin-top: 15px;
}

.team > div {
  display: inline-block;
  width: calc(240px - 3px);
  height: 340px;
  background: rgb(97, 78, 78);
  margin-right: 4px;
  margin-bottom: 4px;
  border-radius: 2px;
}

.team > div:nth-child(4n) {
  margin-right: 0px !important;
}

.team > div > .number {
  display: inline-block;
  width: 40px;
  height: 40px;
  position: relative;
  top: 280px;
  color: #fff;
  font-size: 2rem;
  font-weight: 900;
  text-align: center;
  background: #72142E;
  padding: 10px;
  border-bottom-left-radius: 2px;
}

.team > div > .name {
  float: right;
  width: calc(100% - 80px);
  height: 40px;
  position: relative;
  top: 280px;
  color: #fff;
  font-size: 1.05rem;
  font-weight: 400;
  text-align: left;
  background: #9C1737;
  padding: 10px;
  border-bottom-right-radius: 2px;
}

.fbBox {
  display: block;
  margin-top: 15px;
  padding: 10px;
  color: #fff;
  font-size: 11px;
  background-color: #4268b3;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 2px;
}

.fbBox::before {
  content: url('./images/fb.png');
  width: 20px;
  height: 20px;
  position: absolute;
  margin-top: -3px;
  margin-left: -345px;
}

.single-article {
  display: block;
  width: 960px;
  margin: 15px 0px 0px 0px;
}

.single-article img {
  display: block;
  width: 100%;
  background-color: rgb(119, 119, 119);
}

.single-article h1 {
  display: block;
  width: 930px;
  margin: 0px auto;
  padding: 15px 15px 15px 15px;
  font-size: 19px;
  font-weight: 300;
  text-align: center;
  font-family: 'Merriweather';
  background: white;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}

.single-article .content {
  display: block;
  padding: 15px 15px 15px 15px;
  text-align: justify;
  background: white;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.single-article .content b {
  border-bottom: 1px solid;
}

.single-article .content-text {
  line-height: 1.5;
}

.single-article .news-date {
  display: inline-block;
  margin: 0px auto;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 5px 8px;
  text-align: center;
  text-transform: lowercase;
  color: #fff;
  background: #009688;
  border-radius: 2px;
}

.schedule {
  margin-top: 15px;
}

.schedule div {
  color: #fff;
  background-color: #9C1737;
  padding: 15px;
  text-align: center;
}

.schedule div:nth-child(2n) {
  color: #000;
  background-color: #fff;
  text-transform: uppercase;
  border-left: 1px solid #9C1737;
  border-right: 1px solid #9C1737;
}

.schedule div:nth-child(2n)::after {
  content: '';
  position: relative;
  float: left;
  top: -22px;
  left: calc(50% - 10px);
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-bottom: 7px solid #fff;
  clear: both;
}

.schedule div:first-child {
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}

.schedule div:last-child {
  border-bottom: 1px solid #9C1737;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.ranking {
  padding: 15px;
  background: #fff;
  border-radius: 2px;
}

.ranking table, .ranking table thead, .ranking table thead tr, .ranking table, .ranking table tbody, .ranking table tbody tr {
  display: block;
  width: 100%;
}

.ranking table thead {
  background: orange;
  border-radius: 2px;
}

.ranking table thead tr th, .ranking table tbody tr td {
  padding: 10px 0px;
  text-align: center;
}

.ranking table thead tr th:nth-child(1), .ranking table tbody tr td:nth-child(1) {
  display: inline-block;
  width: 6%;
}

.ranking table thead tr th:nth-child(2), .ranking table tbody tr td:nth-child(2) {
  display: inline-block;
  width: 74%;
  text-align: left;
}

.ranking table thead tr th:nth-child(3), .ranking table tbody tr td:nth-child(3) {
  display: inline-block;
  width: 10%;
}

.ranking table thead tr th:nth-child(4), .ranking table tbody tr td:nth-child(4) {
  display: inline-block;
  width: 10%;
}

.mark {
  color: #fff;
  background-color: #f44336;
  border-radius: 2px;
}